import * as React from 'react';
import { Controller } from '@ali/4ever-cangjie';
import { Button, TableLightNormal, TableNormal } from '@ali/we-design';
import { TableRole as PluginRoles } from '../../../utils/role';
import { insertTable } from '../../actions';
import { ToolbarMode } from '@ali/4ever-component';

export interface TableButtonProps {
  controller: Controller;
  locale: Record<string, string>;
  toolbarMode?: ToolbarMode;
}

export default function TableButton(props: TableButtonProps) {
  const { controller, locale, toolbarMode } = props;
  const isDisabled = !!controller.query('canInsertTable');

  const handleClick = React.useCallback(() => controller.run('onAction', insertTable(3, 3)), [controller]);

  return (
    <Button
      title={locale.buttonTitle}
      bordered={false}
      className="bi-toolbar-table"
      icon={toolbarMode === ToolbarMode.single ? <TableNormal /> : <TableLightNormal />}
      disabled={isDisabled}
      onClick={handleClick}
      role={PluginRoles.table}
    />
  );
}
